import React, { useState, useEffect } from 'react'
import useSiteMeta from '../hooks/useSiteMeta'
// import useTheme from '../hooks/useTheme'
// import styled, { ThemeProvider } from 'styled-components';
import {RiSunFill,RiMoonClearFill} from '@react-icons/all-files/ri/RiMoonClearFill'
// import { FaBeer } from "@react-icons/all-files/fa/FaBeer";
// import ThemeContext from '../store/themeContext'

import { ThemeToggler } from 'gatsby-plugin-dark-mode'
// import { Link } from "gatsby"
// import { darkTheme, lightTheme } from '../theme'
// import { ThemeProvider } from 'react-bootstrap'


const Header = () => {
    // const [theme, changeTheme] = useTheme()
    // const themeMode = theme === 'light' ? lightTheme : darkTheme
    const site = useSiteMeta()
    const [scrolled, setScrolled] = useState(false)

    useEffect(() => {
        const handleScroll = () => {
            if (!scrolled && window.scrollY > 30) {
                setScrolled(true);
            } else if (scrolled && window.scrollY <= 30) {
                setScrolled(false);
            }
        }
        window.addEventListener('scroll', handleScroll);
    
        return () => {
          window.removeEventListener('scroll', handleScroll);
        }
    }, [scrolled])

    return (
        <header className={scrolled ? 'fix-container scrolled' : 'fix-container'}>
            <div className="header">
                <div className="to-home">
                    <a href="/" style={{color:'#9966ff', fontSize:"1.6rem"}}>{site.siteMetadata.title}</a>
                </div>
                <div className="wrap">
                <ThemeToggler>
                    {({ theme, toggleTheme }) => (
                    <div className="switchToggle">
                        <input id="switch"
                            className="invisible switch-checkbox"
                            type="checkbox"
                            onChange={e => toggleTheme(e.target.checked ? 'dark' : 'light')}
                            checked={theme === 'dark'}
                        />
                        {/* <label style={{ margin:0 }} htmlFor="switch" /> */}
                        
                        <label className={`switch-label`} htmlFor={`switch`}>
                            <div><RiMoonClearFill /></div> 
                            {/* <div><RiSunFill /></div>  */}
                        </label>
                    </div>
                    )}
                </ThemeToggler>

                    {/* {mode}
                    <button onClick={lightMode}>
                        <RiMoonClearFill />
                    </button>
                    <button onClick={darkMode}>
                        <RiSunFill />
                    </button> */}
                </div>
                {/* <ThemeProvider theme={themeMode}>
                    <button onClick={changeTheme}></button>
                </ThemeProvider> */}
                {/* <input
                    type="checkbox"
                    name='themeButton'
                    onClick={
                        ()=>{
                            if (localStorage.getItem('theme')==='light') {
                                localStorage.setItem('theme','dark')
                                theme = 'dark'
                                console.log(localStorage.getItem('theme'))
                                // this.setState({
                                //     theme: 'light'
                                // })
                            } else {
                                localStorage.setItem('theme','light')
                                theme = 'light'
                                console.log(localStorage.getItem('theme'))
                                // this.setState({
                                //     theme: 'dark'
                                // })
                            }
                        }
                    } /> */}
                {/* <input
                    type="checkbox"
                    onChange={() => dispatch({ type: 'TOGGLE_MODE' })}
                    checked={state.theme === 'light'}
                    id={`checkbox`}
                    className={`invisible checkbox-checkbox`}
                >
                </input> */}
            </div>
        </header>
    )
}

export default Header;

