import React from "react"
import { Link } from "gatsby"

import Header from "../components/header"
import Footer from "../components/footer"
import logo from "../../content/assets/folder.png";
import { Helmet } from "react-helmet"

const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  const isRootPath = location.pathname === rootPath
  let header = <Link to="/"><img src={logo} width="20" height="20" alt="logo" /></Link>
  
  if (isRootPath) {
    return (
      <div>
        <Helmet>
          <script data-ad-client="ca-pub-5373443176805821" async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"></script>
        </Helmet>
        <Header />
        <main style={{ marginTop:"100px", marginBottom:5, textAlign:"center" }}>{children}</main>
      </div>
    )
  } else {
    return (
      <div>
        <Helmet>
          <script data-ad-client="ca-pub-5373443176805821" async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"></script>
        </Helmet>
        <Header />
        {/* <header style={{ marginTop:"60px", marginBottom:5, textAlign:"center" }}>{header}</header> */}
        <main style={{ marginTop:"170px", marginBottom:5, textAlign:"center" }}>{children}</main>
        <Footer />
      </div>
    )
  }
}

export default Layout
